<template>
  <!-- 歌手详情页 ->  专辑列表 -->
  <div class="album">
    <!-- 左 封面 -->
    <div class="album__left">
      <img :src="album.blurPicUrl" alt="" class="album__left__cover">
    </div>
    <!-- 右 信息 -->
    <div class="album__right">
      <div class="album__right__head">
        <div class="album__right__head__name">{{ album.name }}</div>
        <div class="album__right__head__play iconfont">&#xe632;</div>
        <div class="album__right__head__line">|</div>
        <div class="album__right__head__collect iconfont">&#xe62a;</div>
      </div>
      <!-- 歌曲列表 -->
      <div class="album__right__songs">
        <table class="album__right__songs__table">
        <colgroup class="album__right__songs__table__col">
          <col class="album__right__songs__table__col__1">
          <col class="album__right__songs__table__col__2">
          <col class="album__right__songs__table__col__3">
          <col class="album__right__songs__table__col__6">
        </colgroup>
        <tr v-for="(item, index) in songs" :key="item.id"
        :class="{dark: index % 2 === 0, light: index % 2 === 1, row: true}"
        @dblclick="addMusic(item)">
          <td>
            <div class="album__right__songs__table__cell">
              {{index + 1}}
            </div>
          </td>
          <td>
            <div class="album__right__songs__table__cell iconfont album__right__songs__table__col2">
              &#xe7df;
            </div>
          </td>
          <td>
            <div class="album__right__songs__table__cell">
              {{item.name}}
            </div>
          </td>
          <td>
            <div class="album__right__songs__table__cell">
              {{formatSongTime(item.dt)}}
            </div>
          </td>
        </tr>
      </table>
      </div>
    </div>
  </div>
</template>

<script>
import { getAlbumDetail } from '../../api/api-singer'

export default {
  name: 'AlbumItem',
  data () {
    return {
      album: {},
      songs: []
    }
  },
  // 专辑ID
  props: ['id'],
  methods: {
    async getData () {
      const result = await getAlbumDetail(this.id)
      console.log(result)
      this.album = result.album
      this.songs = result.songs
    },
    // 播放单曲
    addMusic (music) {
      this.$store.commit('playMusic', music)
    },
    // 格式化歌曲长度
    formatSongTime (time) {
      // 补足数字位数
      function preToFixed (num, length = 2) {
        return (Array(length).join(0) + num).slice(-length)
      }
      const length = Math.floor(time / 1000)
      const minute = preToFixed(Math.floor(length / 60))
      const second = preToFixed(length - minute * 60)
      const result = `${minute}:${second}`
      return result
    }
  },
  created () {
    this.getData()
    console.log('item start')
    console.log(this.album, this.songs)
  }
}
</script>

<style lang="scss" scoped>
.album{
  display: flex;
  justify-content: space-between;
  &__left{
    margin-right: 40px;
    &__cover{
      width: 150px;
      height: 150px;
      border-radius: 5px;
    }
  }
  &__right{
    flex: 1;
    &__head{
      display: flex;
      margin-bottom: 10px;
      &__name{
        margin-right: 30px;
        line-height: 30px;
        font-size: 18px;
        font-weight: 600;
        color: #111111;
      }
      &__play{
        margin-right: 10px;
        line-height: 30px;
        font-size: 16px;
        color: #111111;
      }
      &__collect{
        margin-right: 10px;
        line-height: 30px;
        font-size: 16px;
        color: #111111;
      }
      &__line{
        margin-right: 10px;
        line-height: 30px;
        font-size: 16px;
        color: #afafaf;
      }
    }
      &__songs{
        &__table{
          font-size: 12px;
          &__cell{
            width: auto;
            max-width: 100%;
            height: 35px;
            line-height: 35px;
            word-break: break-all;
            overflow: hidden;
          }
          &__col{
            &__1{
              text-align: center;
              width: 55px;
            }
            &__2{
              width: 55px;
            }
            &__3{
              width: 300px;
            }
            &__6{
              width: 70px;
            }
          }
        }
      }
  }
}
.row{
  height: 35px;
  line-height: 35px;
}
td{
  height: 35px;
  line-height: 35px;
}
.dark{
  background-color: #fafafa;
  color: #222222;
}
.light{
  background-color: #ffffff;
}
</style>
