import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/home/Home.vue'
import Recommend from '../views/home/homepage/Recommend.vue'
import Chart from '../views/home/homepage/Chart.vue'
import Login from '../views/login/Login.vue'
import LoginPhone from '../views/login/LoginPhone.vue'
import LoginQR from '../views/login/LoginQR.vue'
import MusicList from '../components/PlayList/MusicList.vue'
import PlayList from '../components/PlayList/PlayList.vue'
import CommentList from '../components/PlayList/CommentList.vue'
import SubscriberList from '../components/PlayList/SubscriberList.vue'
import CatList from '../views/home/homepage/CatList.vue'
import HighQuality from '../views/home/catList/HighQuality.vue'
import Singer from '../views/home/homepage/Singer.vue'
import AlbumList from '../components/Singer/AlbumList.vue'
import SingerDetail from '../components/Singer/SingerDetail.vue'
import MVList from '../components/Singer/MVList.vue'
import SingerDesc from '../components/Singer/SingerDesc.vue'
import SimiSinger from '../components/Singer/SimiSinger.vue'
import RecentSong from '../views/RecentSong.vue'
import LikeSong from '../views/LikeSong.vue'

const routes = [
  // 主页路由
  {
    path: '/',
    name: 'Home',
    component: Home,
    children: [
      {
        path: '',
        component: Recommend
      },
      {
        name: 'Recommend',
        path: 'Recommend',
        component: Recommend
      },
      {
        name: 'Chart',
        path: 'Chart',
        component: Chart
      },
      {
        name: 'CatList',
        path: 'CatList',
        component: CatList
      },
      {
        name: 'Singer',
        path: 'Singer',
        component: Singer
      },
      {
        name: 'HighQuality',
        path: 'HighQuality:cat',
        component: HighQuality
      }
    ]
  },
  // 歌单路由
  {
    path: '/PlayList/:id',
    name: 'PlayList',
    component: PlayList,
    // 子路由  歌曲  评论  收藏者
    children: [
      {
        path: '',
        name: 'MusicList',
        component: MusicList
      },
      {
        path: 'MusicList',
        component: MusicList
      },
      {
        path: 'CommentList',
        component: CommentList
      },
      {
        path: 'SubscriberList',
        component: SubscriberList
      }
    ]
  },
  // 登录页面
  {
    path: '/Login',
    name: 'Login',
    component: Login,
    children: [
      {
        path: 'LoginPhone',
        name: 'LoginPhone',
        component: LoginPhone
      },
      {
        path: 'LoginQR',
        name: 'LoginQR',
        component: LoginQR
      }
    ]
  },
  // 歌手详情页面
  {
    path: '/SingerDetail/:id',
    name: 'SingerDetail',
    component: SingerDetail,
    children: [
      {
        path: '',
        name: 'AlbumList',
        component: AlbumList
      },
      {
        path: 'AlbumList',
        name: 'AlbumList',
        component: AlbumList
      },
      {
        path: 'MVList',
        name: 'MVList',
        component: MVList
      },
      {
        path: 'SingerDesc',
        name: 'SingerDesc',
        component: SingerDesc
      },
      {
        path: 'SimiSinger',
        name: 'SimiSinger',
        component: SimiSinger
      }
    ]
  },
  // test  page
  {
    path: '/test',
    component: CatList
  },
  // 最近播放
  {
    path: '/RecentSong',
    component: RecentSong
  },
  {
    path: '/LikeSong',
    component: LikeSong,
    // 子路由  歌曲  评论  收藏者
    children: [
      {
        path: '',
        name: 'MusicList',
        component: MusicList
      },
      {
        path: 'MusicList',
        component: MusicList
      },
      {
        path: 'CommentList',
        component: CommentList
      },
      {
        path: 'SubscriberList',
        component: SubscriberList
      }
    ]
  },
  // 我的收藏
  {
    path: '/MyCollect',
    component: () => import('../views/MyCollect.vue'),
    children: [
      {
        path: '',
        component: () => import('../views/mycollect/AlbumList.vue')
      },
      {
        path: 'ArtistList',
        component: () => import('../views/mycollect/ArtistList.vue')
      },
      {
        path: 'VideoList',
        component: () => import('../views/mycollect/VideoList.vue')
      }
    ]
  },
  // 视频
  {
    path: '/VideoPage',
    component: () => import('../views/video/VideoPage.vue'),
    children: [
      {
        path: '',
        component: () => import('../views/video/VideoList.vue')
      },
      {
        path: 'MV',
        component: () => import('../views/video/MVList.vue')
      }
    ]
  },
  // 视频播放页
  {
    path: '/VideoPlay/:vid',
    component: () => import('../components/VideoPlay.vue')
  },
  {
    path: '/MVPlay/:id',
    component: () => import('../components/MVPlay.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
