<template>
  <!-- 评论 -->
  <div class="comment">
    <!-- 输入评论 -->
    <div class="comment__add">
      <div class="comment__add__input">
        <textarea type="text" class="comment__add__input__item" maxlength="140">
        </textarea>
      </div>
      <div class="comment__add__commit">
        <div class="comment__add__commit__left">
          <span class="iconfont">&#xe766;&nbsp;</span>
          <span>@&nbsp;</span>
          <span>#</span>
        </div>
        <div class="comment__add__commit__right">评论</div>
      </div>
    </div>
    <!-- 他人评论 -->
    <div class="comment__other">
      <!-- 评论分页 -->
      <template v-for="page in pageNum" :key="page">
        <div class="comment__other__page" v-if="page === currentPage">
          <!-- 精彩评论 -->
          <div class="comment__other__page__title" v-if="pageNum > 1 && page === 1">精彩评论</div>
          <div class="comment__other__page__hot"  v-if="pageNum > 1 && page === 1">
            <div class="comment__other__page__hot__item"
            v-for="(item, index) in hotComments"
            :key="index">
              <div class="comment__other__page__hot__item__left">
                <img :src="item.user.avatarUrl" alt="" class="comment__other__page__hot__item__left__image">
              </div>
              <div class="comment__other__page__hot__item__right">
                <div class="comment__other__page__hot__item__right__text">
                  <span class="comment__other__page__hot__item__right__text__nickname">{{item.user.nickname}}：</span>
                  <span class="comment__other__page__hot__item__right__text__content">{{item.content}}</span>
                </div>
                <div class="comment__other__page__hot__item__right__footer">
                  <div class="comment__other__page__hot__item__right__footer__time">{{item.timeStr}}</div>
                  <div class="comment__other__page__hot__item__right__footer__operate">
                    <span class="comment__other__page__hot__item__right__footer__operate__item iconfont">&#xe7c8;{{item.likedCount}}</span>
                    <span class="comment__other__page__hot__item__right__footer__operate__item iconfont"> &nbsp;|&nbsp; </span>
                    <span class="comment__other__page__hot__item__right__footer__operate__item iconfont">&#xe600;</span>
                    <span class="comment__other__page__hot__item__right__footer__operate__item iconfont"> &nbsp;| &nbsp;</span>
                    <span class="comment__other__page__hot__item__right__footer__operate__item iconfont">&#xe671;</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- 最新评论 -->
          <!-- 每页评论40条，不满40条不渲染 目录 和 精彩评论 -->
          <div class="comment__other__page__title">最新评论</div>
          <div class="comment__other__page__all">
            <div class="comment__other__page__all__item"
            v-for="(item, index) in comments.slice((page-1)*40,page*40)"
            :key="index">
              <div class="comment__other__page__all__item__left">
                <img :src="item.user.avatarUrl" alt="" class="comment__other__page__all__item__left__image">
              </div>
              <div class="comment__other__page__all__item__right">
                <div class="comment__other__page__all__item__right__text">
                  <span class="comment__other__page__all__item__right__text__nickname">{{item.user.nickname}}：</span>
                  <span class="comment__other__page__all__item__right__text__content">{{item.content}}</span>
                </div>
                <div class="comment__other__page__all__item__right__footer">
                  <div class="comment__other__page__all__item__right__footer__time">{{item.timeStr}}</div>
                  <div class="comment__other__page__all__item__right__footer__operate">
                    <span class="comment__other__page__all__item__right__footer__operate__item iconfont">&#xe7c8;{{item.likedCount}}</span>
                    <span class="comment__other__page__all__item__right__footer__operate__item iconfont"> &nbsp;| &nbsp;</span>
                    <span class="comment__other__page__all__item__right__footer__operate__item iconfont">&#xe600;</span>
                    <span class="comment__other__page__all__item__right__footer__operate__item iconfont"> &nbsp;| &nbsp;</span>
                    <span class="comment__other__page__all__item__right__footer__operate__item iconfont">&#xe671;</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </template>
      <!-- 页码目录 -->
      <div class="comment__other__page-list"></div>
    </div>
  </div>
</template>

<script>
import { getComment } from '../../api/api-playlist'

export default {
  name: 'CommentList',
  data () {
    return {
      id: this.$route.params.id,
      currentPage: 1,
      pageNum: 1,
      hotComments: [],
      comments: []
    }
  },
  methods: {
    async getData () {
      const result = await getComment(this.id)
      this.hotComments = result.hotComments
      this.comments = result.comments
      const total = result.total
      if (total > 40) {
        this.pageNum = Math.ceil(total / 40)
      }
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.comment{
  padding:20px;
  width: 100%;
  &__add{
    margin-bottom: 40px;
    &__input{
      margin-bottom: 10px;
      &__item{
      padding:5px;
      width: 100%;
      height: 70px;
      border-radius: 5px;
      border: 1px solid #5f5f5f;
      font-size: 14px;
      resize: none;
      }
    }
    &__commit{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &__left{
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &__right{
        padding:5px 10px;
        border: 1px solid #5f5f5f;
        border-radius: 13px;
        font-size: 16px;
        cursor: pointer;
      }
    }
    &__right:hover{
      background-color: #f2f2f2;
    }
  }
  &__other{
    &__page{
      &__title{
        font-size: 16px;
        font-weight: 600;
        color:#111111;
      }
      &__hot{
        &__item{
          display: flex;
          margin: 20px 0;
          padding-bottom: 20px;
          border-bottom: 1px solid #f1f1f1;
          &__left{
            margin-right: 10px;
            &__image{
            width: 40px;
            height: 40px;
            border-radius: 20px;
            }
          }
          &__right{
            flex:1;
            font-size: 12px;
            &__text{
              margin-bottom: 10px;
              &__nickname{
                color:#0b58b0;
              }
            }
            &__footer{
              display: flex;
              align-items: center;
              justify-content: space-between;
            }
          }
        }
      }
      &__all{
        &__item{
          display: flex;
          margin: 20px 0;
          padding-bottom: 20px;
          border-bottom: 1px solid #f1f1f1;
          &__left{
            margin-right: 10px;
            &__image{
            width: 40px;
            height: 40px;
            border-radius: 20px;
            }
          }
          &__right{
            flex:1;
            font-size: 12px;
            &__text{
              margin-bottom: 10px;
              &__nickname{
                color:#0b58b0;
              }
            }
            &__footer{
              display: flex;
              align-items: center;
              justify-content: space-between;
            }
          }
        }
      }
    }
  }
}
</style>
