<template>
  <div class="container">
    <div v-for="item in albums"
    :key="item.id"
    class="container__item">
      <AlbumItem :id = 'item.id'></AlbumItem>
    </div>
  </div>
</template>

<script>
import { getAlbum } from '../../api/api-singer'
import AlbumItem from './AlbumItem.vue'

export default {
  name: 'AlbumList',
  components: { AlbumItem },
  data () {
    return {
      // 歌手ID
      id: this.$route.params.id,
      albums: []
    }
  },
  methods: {
    async getData () {
      console.log(this.id)
      const result = await getAlbum(this.id)
      console.log(result, 'getdta')
      this.albums = result.hotAlbums
    }
  },
  created () {
    this.getData()
    console.log('list start')
  },
  mounted () {
    console.log('mounted')
    console.log(this.albums, 'mounted')
  }
}
</script>

<style lang="scss" scoped>
.container__item{
  margin-bottom: 40px;
}
</style>
