<template>
  <!-- 歌单详情面 -->
  <div class="detail">
    <!-- 头部歌单信息 -->
    <div class="detail__head">
      <!-- 左边图片 -->
      <div class="detail__head__left">
        <img :src="playList.coverImgUrl" alt="" class="detail__head__left__cover">
      </div>
      <!-- 右边信息 -->
      <div class="detail__head__right">
        <!-- 标题    -->
        <div class="detail__head__right__title">
          <span class="detail__head__right__title__mark">歌单</span>
          <h3 class="detail__head__right__title__name">{{playList.name}}</h3>
        </div>
        <!-- 创建者信息   -->
        <div class="detail__head__right__creator">
          <img :src="creator.avatarUrl" alt="" class="detail__head__right__creator__avatar">
          <span class="detail__head__right__creator__nickname">{{creator.nickname}}</span>
          <span class="detail__head__right__creator__date">{{formatDate(playList.createTime)}}</span>
        </div>
        <!-- 操作   -->
        <div class="detail__head__right__operate">
          <!-- 播放 -->
          <div class="detail__head__right__operate__play">
            <span class="detail__head__right__operate__play__left iconfont" @click="playAllMusic(true)">&#xe717;播放全部</span>
            <span class="detail__head__right__operate__play__right iconfont">&#xe8fe;</span>
          </div>
          <!-- collect  -->
          <div class="detail__head__right__operate__collect iconfont">
            &#xe62a;收藏({{playList.subscribedCount}})
          </div>
          <!-- share  -->
          <div class="detail__head__right__operate__share iconfont">
            &#xe600;分享({{playList.shareCount}})
          </div>
        </div>
        <!-- 标签   -->
        <div class="detail__head__right__tags">
          标签：{{formatTags(playList.tags)}}
        </div>
        <!-- 歌曲数量  播放次数 -->
        <div class="detail__head__right__count">
          <span class="detail__head__right__count__song">歌曲：{{playList.trackCount}}</span>
          <span class="detail__head__right__count__play">播放：{{playList.playCount}}</span>
        </div>
        <!-- 简介   -->
        <div class="detail__head__right__intro">简介：{{playList.description}}</div>
      </div>
    </div>
    <!-- 内容 -->
    <div class="detail__body">
      <!-- 路由 分别导向  歌曲 评论 收藏者 -->
      <div class="detail__body__router">
        <router-link
        :class="{detail__body__router__item:true, 'detail__body__router__item-active':item.id === currentId}"
        @click="handleChangeRouter(item.id)"
        v-for="item in routers"
        :key="item.id"
        :to="`/playlist/${id}/${item.router}`">
          {{item.name}}
        </router-link>
      </div>
      <!-- 路由显示 -->
      <div class="detail__body__view">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script lang="js">
import { getPlayListDetail, getTrackAll } from '../../api/api-playlist'

export default {
  name: 'PlayList',
  data () {
    return {
      id: this.$route.params.id,
      playList: {},
      creator: {},
      currentId: 1,
      routers: [
        {
          id: 1,
          name: '歌曲列表',
          router: 'MusicList'
        },
        {
          id: 2,
          name: '评论',
          router: 'CommentList'
        },
        {
          id: 3,
          name: '收藏者',
          router: 'SubscriberList'
        }
      ]
    }
  },
  methods: {
    // 播放所有歌曲
    async playAllMusic (toggle) {
      const result = await getTrackAll(this.id)
      const songList = result.songs
      this.$store.commit('playAllMusic', { songList, toggle })
    },
    // 获取歌单详情
    async getData (id) {
      const result = await getPlayListDetail(id)
      console.log(result.playlist)
      console.log(result.playlist.creator)
      this.playList = result.playlist
      this.creator = result.playlist.creator
    },
    // 格式化日期
    formatDate (time) {
      const date = new Date(time)
      const year = date.getFullYear()
      const month = date.getMonth()
      const day = date.getDay()
      const result = `${year}-${month}-${day}创建`
      return result
    },
    // 格式化标签
    formatTags (tags) {
      let result = ''
      console.log('tags.length', tags.length)
      for (let index = 0; index < tags.length; index++) {
        result += tags[index]
        if (index !== tags.length - 1) {
          result += '/'
        }
      }
      return result
    },
    handleChangeRouter (id) {
      this.currentId = id
    }
  },
  created () {
    // 在创建钩子中获得数据
    console.log(this.id, 'playlist id')
    this.getData(this.id)
  }
}
</script>

<style lang="scss" scoped>

@import '../../assets/styles/mixins.scss';
.detail{
  padding:30px;
  &__head{
    display: flex;
    margin-bottom: 40px;
    &__left{
      width: 184px;
      height: 184px;
      margin-right: 20px;
      &__cover{
        width: 100%;
        height: 100%;
      }
    }
    &__right{
      flex:1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      &__title{
        display: flex;
        align-items: center;
        &__mark{
          border: 1px solid #ec4141;
          padding:1px 5px;
          border-radius: 2px;
          font-size: 12px;
          color:#ec4141;
          margin-right: 20px;
        }
      }
      &__creator{
        display: flex;
        align-items: center;
        height: 26px;
        &__avatar{
          margin-right: 5px;
          width: 26px;
          height: 26px;
          border-radius: 14px;
        }
        &__nickname{
          margin-right: 10px;
          font-size: 12px;
          color:#abeaff;
        }
        &__date{
          font-size: 12px;
          color:#676767;
        }
      }
      &__operate{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        &__play{
          height: 32px;
          border-radius: 16px;
          color:white;
          background-color: #ec4141;
          line-height: 32px;
          margin-right: 10px;
          cursor: pointer;
          &__left{
            display: inline-block;
            height: 32px;
            border-radius:16px  0 0 16px;
            border-right: 1px solid rgba(#ffffff,0.5);
            padding:0 5px 0 16px;
          }
          &__right{
            display: inline-block;
            height: 32px;
            border-radius:  0 16px 16px 0;
            padding: 0 10px 0;
          }
        }
        &__play:hover{
          background-color: #d73535;
          &__left:hover{
            background-color: #cc3232;
          }
          &__right:hover{
            background-color: #cc3232;
          }
        }
        &__collect{
          margin-right: 10px;
          height: 32px;
          border: 1px solid #d9d9d9;
          border-radius: 16px;
          padding: 0 16px;
          line-height: 32px;
          font-size: 14px;
          cursor: pointer;
        }
        &__collect:hover{
          background-color: #f2f2f2;
        }
        &__share{
          height: 32px;
          border: 1px solid #d9d9d9;
          border-radius: 16px;
          padding: 0 16px;
          line-height: 32px;
          font-size: 14px;
          cursor: pointer;
        }
        &__share:hover{
          background-color: #f2f2f2;
        }
      }
      &__tags{
        font-size: 12px;
        color:#676767;
      }
      &__count{
        font-size: 12px;
        color:#676767;
      }
      &__intro{
        font-size: 12px;
        width: 500px;
        @include ellipsis;
      }
    }
  }
  &__body{
    &__router{
      display: flex;
      align-items: center;
      &__item{
        position: relative;
        font-size: 16px;
        font-weight: 300px;
        margin-right: 20px;
        color: #111111;
        &-active{
          font-size: 18px;
          font-weight: 700;
        }
        &-active::after{
          display: block;
          content: '';
          margin: 0 auto;
          width: 90%;
          height: 4px;
          border-radius: 2px;
          background-color: #ec4141;
        }
      }
    }
  }
}
</style>
