<template>
  <!-- 左边菜单 -->
  <div class="aside">
    <div :class="{aside__item:item.text!=='我的音乐','aside__item-active':item.text===currentItemName,'aside__item-title':item.text=='我的音乐',iconfont:true}"
    v-for="item in asideList"  :key="item.id"
    @click="changeCurrentItem(item)"
    v-html="item.text">
    </div>
  </div>
</template>
<script>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
// 菜单数据
const useEffect = () => {
  const asideList = [
    {
      id: 1,
      text: '发现音乐',
      router: '/'
    },
    {
      id: 2,
      text: '视频',
      router: '/VideoPage'
    },
    {
      id: 3,
      text: '关注',
      router: ''
    },
    {
      id: 4,
      text: '私人FM',
      router: ''
    },
    {
      id: 5,
      text: '我的音乐',
      router: ''
    },
    {
      id: 6,
      text: '&#xe7df;&nbsp;我喜欢的音乐',
      router: '/LikeSong'
    },
    {
      id: 7,
      text: '&#xe90e;&nbsp;最近播放',
      router: '/RecentSong'
    },
    {
      id: 8,
      text: '&#xe7ae;&nbsp;我的收藏',
      router: '/MyCollect'
    }
  ]
  const router = useRouter()
  const currentItemName = ref('发现音乐')
  const changeCurrentItem = (val) => {
    if (val.text !== '我的音乐') {
      currentItemName.value = val.text
      router.push(val.router)
    }
  }
  return { asideList, currentItemName, changeCurrentItem }
}
export default {
  name: 'AsideMenu',
  setup () {
    const { asideList, currentItemName, changeCurrentItem } = useEffect()
    return { asideList, currentItemName, changeCurrentItem }
  }
}
</script>
<style lang="scss" scoped>
.aside{
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 70px;
  width: 200px;
  padding:10px 5px 0 10px;
  border-right: 1px solid #e0e0e0;
  &__item{
    margin-top: 5px;
    border-radius: 1px;
    padding:10px;
    height: 40px;
    line-height: 20px;
    font-size:16px;
    color: black;
    text-align: start;
    cursor: pointer;
  }
  &__item-title{
    margin-top: 5px;
    border-radius: 1px;
    padding-left:10px;
    height: 30px;
    line-height: 30px;
    font-size:14px;
    color: #a1a1a1;
    text-align: start;
    cursor: pointer;
  }
  &__item:hover{
    background-color: rgba(0,0,0,0.1);
  }
  &__item-active{
    background-color: rgba(0,0,0,0.1);
  }
}
</style>
