<template>
  <!-- 头部菜单 -->
    <header class="header">
      <!-- 左边标题 -->
      <div class="header__left">
        <h3 class="header__left__title">
          <span class="header__left__title__icon iconfont">&#xeeab;</span>
          网易云音乐
        </h3>
      </div>
      <!-- 右边菜单 -->
      <div class="header__right">
        <div class="header__right__item">
          <!-- 搜索加前进后退 -->
          <div class="header__right__item__left ">
            <span class="header__right__item__left__icon-item header__right__item__left__pre iconfont" @click="goRouterHistory(-1)">&#xe666;</span>
            <span class="header__right__item__left__icon-item header__right__item__left__back iconfont" @click="goRouterHistory(1)">&#xe665;</span>
            <div class="header__right__item__left__search">
              <span class="header__right__item__left__search__icon iconfont">&#xe648;</span>
              <input type="text" class="header__right__item__left__search__input">
            </div>
          </div>
          <!-- 其他菜单按钮 功能暂不实现-->
          <div class="header__right__item__right ">
            <!-- 登录信息 -->
            <div class="header__right__item__right__user" v-if="isLogin">
              <img :src="avatarUrl" alt="" class="header__right__item__right__user__avator">
              <span class="header__right__item__right__user__nickname">{{nickname}}</span>
            </div>
            <!-- 未登录 -->
            <router-link to="/Login/LoginPhone" v-else>
              <div class="header__right__item__right__log-out" >
                <span class="header__right__item__right__log-out__icon iconfont">&#xe68b;</span>
                <span class="header__right__item__right__log-out__text">未登录</span>
              </div>
            </router-link>
            <span class="header__right__item__right__icon-item header__right__item__right__theme iconfont">&#xe6c6;</span>
            <span class="header__right__item__right__icon-item header__right__item__right__setting iconfont">&#xe6f0;</span>
            <span class="header__right__item__right__icon-item header__right__item__right__message iconfont">&#xe70a;</span>
            <span class="header__right__item__right__icon-item header__right__item__right__interval iconfont"> | </span>
            <span class="header__right__item__right__icon-item header__right__item__right__mini iconfont">&#xe6b7;</span>
            <span class="header__right__item__right__icon-item header__right__item__right__max iconfont">&#xe6ba;</span>
            <span class="header__right__item__right__icon-item header__right__item__right__min iconfont">&#xe601;</span>
            <span class="header__right__item__right__icon-item header__right__item__right__close iconfont" @click="logout" title="退出登录">&#xe6dc;</span>
          </div>
        </div>
      </div>
    </header>
</template>
<script>
import { getLogout } from '../api/api_login'
import { mapState } from 'vuex'

export default {
  name: 'HeaderMenu',
  computed: {
    ...mapState({
      isLogin: state => state.isLogin,
      nickname: state => state.user.profile.nickname,
      avatarUrl: state => state.user.profile.avatarUrl
    })
  },
  methods: {
    getUserInfo () {
      this.isLogin = this.$store.state?.isLogin
      this.userName = this.$store.state?.profile?.nickname
      this.avatarUrl = this.$store.state?.profile?.avatarUrl
    },
    logout () {
      getLogout()
      this.$store.commit('logout')
    },
    goRouterHistory (val) {
      this.$router.go(val)
    }
  },
  created () {
    console.log(this.isLogin)
    // this.getUserInfo()
  }
}
</script>
<style lang="scss" scoped>
@import '../assets/styles/mixins.scss';
.header{
  display: flex;
  justify-content: space-between;
  position: absolute;
  top:0;
  left: 0;
  right: 0;
  height: 60px;
  background-color: #ec4141;
  &__left{
    // 左边标题
    // margin-left: 20px;
    padding-left: 20px;
    height: 60px;
    width: 200px;
    text-align: left;
    &__title{
      height: 60px;
      line-height: 60px;
      font-size: 20px;
      color:white;
      &__icon{
        height: 60px;
        vertical-align: middle;
        line-height: 60px;
        font-size: 30px;
        color:white;
      }
    }
  }
  // 右边菜单
  &__right{
    flex:1;
    &__item{
      display: flex;
      justify-content: space-between;
      align-items: center;
      line-height: 60px;
      // 菜单左半部分
      &__left{
        display: flex;
        align-items: center;
        &__icon-item{
          margin-right: 10px;
          height: 26px;
          line-height: 26px;
          border-radius: 13px;
          font-size: 26px;
          color:white;
          background-color: rgba(0,0,0, 0.1);
          cursor:pointer;
        }
        &__search{
          position: relative;
          &__icon{
            position: absolute;
            left: 10px;
            color:white;
          }
          &__input{
            border: none;
            border-radius: 15px;
            padding-left: 30px;
            height: 30px;
            width: 160px;
            background-color: rgba(0,0,0, 0.1);
          }
        }
      }
      // 菜单右半部分
      &__right{
        display: flex;
        align-items: center;
        &__user{
          display: flex;
          align-items: center;
          justify-content: center;
          margin-right: 20px;
          &__avator{
            border-radius: 14px;
            width: 28px;
            height: 28px;
            margin-right: 5px;
          }
          &__nickname{
            display: inline-block;
            width: 80px;
            @include ellipsis;
            font-size: 12px;
            color: white;
          }
        }
        // 未登录
        &__log-out{
          display: flex;
          align-items: center;
          justify-content: center;
          margin-right: 20px;
          color:white;
          &__icon{
            margin-right: 10px;
            font-size: 28px;
          }
          &__text{
            color:white;
          }
        }
        &__icon-item{
          margin-right: 20px;
          font-size: 20px;
          color:white;
          cursor: pointer;
        }
      }
    }
  }
}
</style>
