<template>
  <!-- 发现音乐 -> 歌单 -> 头部精品歌单 -->
  <div class="banner" :style="{'backgroundImage': 'url('+banner.coverImgUrl+')'}" @click="pushRouter(cat)">
    <div class="banner__cover">
      <img :src="banner.coverImgUrl" alt="" class="banner__cover__image">
    </div>
    <div class="banner__title">
      <div class="banner__title__type">精品歌单</div>
      <div class="banner__title__name">{{ banner.name}}</div>
      <div class="banner__title__writer">{{ banner.copywriter}}</div>
    </div>
  </div>
</template>

<script>
import { getHighQuality } from '../../../api/api-playlist'
export default {
  name: 'CatBanner',
  data () {
    return {
      banner: {}
    }
  },
  props: ['cat'],
  watch: {
    cat (val) {
      this.getData()
    }
  },
  methods: {
    async getData () {
      console.log(this.cat)
      const result = await getHighQuality(this.cat, 1)
      this.banner = result.playlists[0]
      console.log('getbanner')
    },
    pushRouter (cat) {
      this.$router.push({ name: 'HighQuality', params: { cat: this.cat } })
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.banner{
  display: flex;
  align-items: center;
  height: 170px;
  border-radius: 5px;
  padding: 15px;
  // background-size: cover;
  // filter:blur(5px);
  &__cover__image{
    width: 140px;
    height: 140px;
    border-radius: 5px;
  }
  &__title{
    flex: 1;
    margin-left: 10px;
    &__type{
      margin-bottom: 24px;
      height: 30px;
      width: fit-content;
      padding: 0 15px;
      border: 1px solid #e7aa5a;
      border-radius: 15px;
      font-size: 16px;
      color: #e7aa5a;
    }
    &__name{
      font-size: 16px;
      color: white;
    }
    &__writer{
      font-size: 12px;
      color: #85848d;
    }
  }
}
</style>
