<template>
  <!-- 发现音乐 ->  歌单 -> 精品歌单 -->
  <div class="boutique">
    <!-- 头部 -->
    <div class="boutique__head">
      <div class="boutique__head__title">精品歌单</div>
      <div class="boutique__head__tag iconfont" @click="showTags = !showTags">&#xe78f;{{ currentTag}}</div>
    </div>
    <!-- 标签列表 -->
      <div class="boutique__taglist" v-if="showTags">
        <div class="boutique__taglist__all" @click="changeCurrentTag('全部歌单')">全部歌单</div>
        <div class="boutique__taglist__swap" >
          <div class="boutique__taglist__swap__item"
          v-for="item in tags"
          :key="item.id"
          @click="changeCurrentTag(item.name)">
            {{ item.name }}
          </div>
        </div>
      </div>
      <!-- 歌单列表 -->
    <div class="boutique__list">
      <div class="boutique__list__item"
      v-for="item in lists"
      :key="item.id">
      <HighCover :id="item.id"
      :playCount="item.playCount"
      :name="item.name"
      :nickname="item.creator.nickname"
      :coverImgUrl="item.coverImgUrl"
      :copywriter="item.copywriter"></HighCover>
      </div>
    </div>
  </div>
</template>

<script>
import { getHighQuality, getHighQualityTag } from '../../../api/api-playlist'
import HighCover from './HighCover.vue'
export default {
  name: 'HighQuality',
  components: { HighCover },
  data () {
    return {
      // 歌单列表
      lists: [],
      cat: this.$route.params.cat,
      // 当前分类
      currentTag: this.$route.params.cat,
      // 分类列表
      tags: null,
      showTags: false
    }
  },
  watch: {
    currentTag (val) {
      this.getData(val)
    }
  },
  methods: {
    async getData (cat) {
      let result = await getHighQuality(cat)
      this.lists = result.playlists
      if (!this.tags) {
        result = await getHighQualityTag()
        this.tags = result.tags
      }
    },
    changeCurrentTag (val) {
      this.currentTag = val
      this.showTags = false
    }
  },
  created () {
    this.getData(this.cat)
  }
}
</script>

<style lang="scss" scoped>
.boutique{
  position: relative;
  &__taglist{
      position: absolute;
      padding: 30px;
      right: 30px;
      top: 30px;
      width: 500px;
      background-color: white;
      border: 1px solid #e1e1e1;
      border-radius: 5px;
      z-index: 10;
    &__all{
      margin-bottom: 30px;
      line-height: 50px;
      height: 50px;
      border-bottom: 1px solid #e1e1e1;
      font-size: 16px;
      cursor: pointer;
    }
    &__all:hover{
      color: #f10000;
    }
    &__swap{
      display: grid;
      grid-template-columns: repeat(5,1fr);
      gap: 20px;
      &__item{
        font-size: 14px;
        color: #222222;
        cursor: pointer;
      }
      &__item:hover{
        color: #f10000;
      }
    }
  }
  &__head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    &__title{
      font-size: 20px;
      color: #1a1a1a;
      font-weight: 500;
    }
    &__tag{
      box-sizing: content-box;
      height: 16px;
      line-height: 16px;
      border: 1px solid #f1f1f1;
      border-radius: 16px;
      padding: 2px 10px;
      font-size: 14px;
      color: #5f5f5f;
      cursor: pointer;
    }
    &__tag:hover{
      background-color: #e1e1e1;
    }
  }
  &__list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}
</style>
