<template>
  <div class="container">
    <div v-for="item in mvs"
    :key="item.id"
    class="container__item">
      <MVItemVue :item = "item"></MVItemVue>
    </div>
  </div>
</template>

<script>
import { getMV } from '../../api/api-singer'
import MVItemVue from './MVItem.vue'
export default {
  name: 'MVList',
  components: { MVItemVue },
  data () {
    return {
      id: this.$route.params.id,
      mvs: []
    }
  },
  methods: {
    async getData () {
      const result = await getMV(this.id)
      this.mvs = result.mvs
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.container{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 20px;
}
</style>
