<template>
  <div class="qr">
    <img :src="qrimg" alt="" class="qr__image">
    <p class="qr__notice">{{notice}}</p>
  </div>
</template>
<script>
import { getKey, getQR, checkQR } from '../../api/api_login'
// const useLoginEffect = () => {
//   let qrimg = ''
//   const useQR = () => {
//   useQR()
//   return { qrimg }
// }
export default {
  name: 'LoginQR',
  data () {
    return {
      qrimg: '',
      notice: '请使用网易云音乐app扫码登录',
      key: ''
    }
  },
  methods: {
    async useQR () {
      // 获取key
      let result = await getKey()
      console.log('start getkey', result)
      if (result.code !== 200) { this.notice = '获取二维码失败'; return }
      this.key = result.data.unikey
      // 获取二维码
      result = await getQR()
      if (result.code !== 200) { this.notice = '生成二维码失败'; return }
      this.qrimg = result.data.qrimg
      this.timeOut = setInterval(
        this.pollCheck, 1000
      )
    },
    // 轮询二维码状态
    async pollCheck () {
      const result = await checkQR()
      if (result.code === 800) {
        this.notice = '二维码已过期'; return null
      } else if (result.code === 801) {
        this.notice = '请使用网易云音乐app扫码登录'
        return null
      } else if (result.code === 802) {
        this.notice = '请在app确认登陆'
        return null
      } else if (result.code === 803) {
        clearInterval(this.timeOut)
        this.$router.push('/')
      }
    }
  },
  created () {
    this.useQR()
  },
  // 在组件卸载时清除定时器
  unmounted () {
    clearInterval(this.timeOut)
  }
}
</script>
<style lang="scss">
.qr__notice{
  margin-top: 20px;
}
</style>
