<template>
  <!-- 发现音乐  >   排行榜页面 -->
  <div class="chart">
    <!-- 官方榜 -->
    <div class="chart__authority">
      <div class="chart__authority__title">官方榜</div>
      <div class="chart__authority__item"
      v-for="item in authorityList"
      :key="item.id">
        <!-- 封面 -->
        <div class="chart__authority__item__cover" @click="this.$router.push({name: 'PlayList', params: {id: item.id}})">
          <div class="chart__authority__item__cover__date">
            {{ new Date(item.trackNumberUpdateTime).getMonth() }}月
            {{ new Date(item.trackNumberUpdateTime).getDate() }}日更新
          </div>
          <img :src="item.coverImgUrl" alt="" class="chart__authority__item__cover__image">
        </div>
        <!-- 信息  前五  的歌   左歌名 右歌手 -->
        <div class="chart__authority__item__detail">
          <div :class="{chart__authority__item__detail__item:true, 'chart__authority__item__detail__item-dark': index % 2 === 0, 'red-text': index < 4}"
          v-for="(order, index) in item.tracks"
          :key="index"
          @dblclick="addMusic(order)">
            <div class="chart__authority__item__detail__item__left">
              <span :class="{chart__authority__item__detail__item__left__order:true,
              'top-one': index === 0,
              'top-two': index === 1,
              'top-three': index === 2,
              }">{{ index + 1 }} </span>
              <span class="chart__authority__item__detail__item__left__song"> &nbsp;&nbsp;{{ order.name }}</span>
            </div>
            <div class="chart__authority__item__detail__item__right">{{ order.ar[0].name }}</div>
          </div>
          <!-- 进入详情页 -->
          <div class="chart__authority__item__detail__more iconfont"
          @click="this.$router.push({name: 'PlayList', params: {id :item.id}})">查看全部 &#xe665;</div>
        </div>
      </div>
    </div>
    <!-- 全球榜 -->
    <div class="chart__other">
      <div class="chart__other__title">全球榜</div>
      <div class="chart__other__container">
      <PlayListCover v-for="item in otherList"
      :key="item.id"
      :id = "item.id"
      :playCount = "item.playCount"
      :title = "item.name"
      :imgabc = "item.coverImgUrl"></PlayListCover>
      </div>
    </div>
  </div>
</template>

<script>
import { getTopList, getDetail } from '../../../api/api-chart'
import PlayListCover from '../../../components/PlayList/PlayListCover.vue'
export default {
  name: 'Chart',
  components: { PlayListCover },
  data () {
    return {
      authorityList: [],
      otherList: []
    }
  },
  methods: {
    async getData () {
      const result = await getTopList()
      const list = result.list
      console.log(list)
      this.authorityList = list.slice(0, 4)
      this.otherList = list.slice(4)
      for (const item of this.authorityList) {
        const res = await getDetail(item.id)
        const tracks = res.playlist.tracks
        tracks.length = 5
        item.tracks = tracks
      }
    },
    addMusic (music) {
      this.$store.commit('playMusic', music)
    }
  },
  created () {
    this.getData()
    console.log(this.authorityList)
    console.log(this.otherList)
  }
}
</script>

<style lang="scss" scoped>
.chart{
  &__authority{
    &__title{
      font-size: 20px;
      color:#111111;
      font-weight: 500;
      margin-bottom: 18px;
    }
    &__item{
      display: flex;
      margin-bottom: 30px;
      &__cover{
        position: relative;
        margin-right: 18px;
        &__image{
        width: 172px;
        height: 172px;
        border-radius: 5px;
        }
        &__date{
          position: absolute;
          top: 100px;
          left: 50%;
          transform: translateX(-50%);
          width: fit-content;
          font-size: 12px;
          color:white;
        }
      }
      &__detail{
        flex: 1;
        &__item{
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 0 10px;
          height: 35px;
          background-color: white;
          &__left{
            line-height: 35px;
            font-size: 12px;
            color: #0f0f0f;
          }
          &__right{
            line-height: 35px;
            font-size: 12px;
            color: #555555;
          }
          cursor: inherit;
        }
        &__item-dark{
          background-color: #fafafa;
        }
        &__item:hover{
          background-color: #f5f5f5;
        }
        &__more{
          padding: 0 10px;
          height: 35px;
          width: fit-content;
          font-size: 12px;
          line-height: 35px;
          color: #333333;
          cursor: pointer;
        }
      }
    }
  }
  &__other{
    &__title{
    font-size: 20px;
      color:#111111;
      font-weight: 500;
      margin-bottom: 18px;
    }
    &__container{
      display: grid;
      grid-template-columns: repeat(5,1fr);
      gap: 20px;
    }
  }
}
.top-one{
  color: #ff0000;
}
.top-two{
  color: #dd0000;
}
.top-three{
  color: #cc0000;
}
</style>
