<template>
  <CurrentPlayListVue v-if="showMusicList"></CurrentPlayListVue>
  <!-- 底部播放组件 -->
  <div class="footer">
    <!-- 左边歌曲信息 -->
    <div class="footer__info">
      <!-- 歌曲图片 -->
      <div class="footer__info__left">
        <img :src="coverUrl" alt="" class="footer__info__left__image">
      </div>
      <!-- 歌名和歌手 -->
      <div class="footer__info__right">
        <div class="footer__info__right__song">
          <span class="footer__info__right__song__name">{{info.song}}</span>
          <span class="footer__info__right__song__icon iconfont">&#xe7df;</span>
        </div>
        <div class="footer__info__right__singer">{{info.singer}}</div>
      </div>
    </div>
    <!-- 中间播放控件 -->
    <div class="footer__player">
      <!-- 按钮 -->
      <div class="footer__player__button">
        <!-- 播放按钮 -->
        <span class="footer__player__button__previous iconfont"
        @click="changeMusic(-1)">&#xe610;</span>
        <span class="footer__player__button__play iconfont" v-show="!isPlay"
        @click="playMusic">&#xe717;</span>
        <span class="footer__player__button__play iconfont" v-show="isPlay"
        @click="pauseMusic">&#xe7fe;</span>
        <span class="footer__player__button__next iconfont"
        @click="changeMusic(1)">&#xe611;</span>
      </div>
      <!-- 进度条 -->
      <div class="footer__player__progress">
        <div class="footer__player__progress__current-time">
          {{formatCurrentTime}}
        </div>
          <input type="range" min="0" :max="totalTime / 1000"  v-model="currentTime" class="footer__player__progress__item" @change="changeCurrentTime">
        <div class="footer__player__progress__total-time">
          {{formatSongTime(totalTime)}}
        </div>
      </div>
    </div>
    <!-- 右边选项菜单 -->
    <div class="footer__menu">
      <div class="footer__menu__volume iconfont">
        &#xe6e3;
        <input v-model="volume" type="range" min="0" max="1"  step="0.01" class="footer__menu__volume__range">
      </div>
      <div class="footer__menu__playlist iconfont" @click="changeShowMusicList()">
        &#xe9ff;
      </div>
    </div>
    <!-- 播放器 -->
    <audio ref="audio" :src="currentSongUrl" @ended="nextMusic(1)" @timeupdate="updateCurrentTime" ></audio>
  </div>
</template>

<script>
import { mapState, useStore } from 'vuex'
import { getSongUrl } from '../api/api-playlist'
import CurrentPlayListVue from './CurrentPlayList.vue'

// 音乐控件逻辑
const useChangeMusicEffect = () => {
  const store = useStore()
  const playMusic = () => {
    store.commit('setIsPlay', true)
    console.log(this.musicList)
  }
  const pauseMusic = () => {
    store.commit('setIsPlay', false)
  }
  const changeMusic = (value) => {
    store.commit('changeMusic', value)
  }
  const nextMusic = (value) => {
    store.commit('changeMusic', value)
  }
  const formatSongTime = (time) => {
    // 补足数字位数
    function preToFixed (num, length = 2) {
      return (Array(length).join(0) + num).slice(-length)
    }
    const length = Math.floor(time / 1000)
    const minute = preToFixed(Math.floor(length / 60))
    const second = preToFixed(length - minute * 60)
    const result = `${minute}:${second}`
    return result
  }
  return { playMusic, pauseMusic, changeMusic, formatSongTime, nextMusic }
}
export default {
  name: 'FooterPlayer',
  components: { CurrentPlayListVue },
  data () {
    return {
      // 当前歌曲URL
      curretnSongUrl: '',
      currentTime: 0,
      totalTime: 0,
      coverUrl: require('../assets/images/music-cover.jpg'),
      currentSongUrl: '',
      currentSong: {},
      volume: 0.5,
      showMusicList: false,
      // 歌曲信息
      info: {
        song: '未知歌名',
        singer: '未知歌手'
      }
    }
  },
  setup () {
    const { playMusic, pauseMusic, changeMusic, formatSongTime, nextMusic } = useChangeMusicEffect()
    return { playMusic, pauseMusic, changeMusic, formatSongTime, nextMusic }
  },
  computed: {
    // get vuex  data
    ...mapState({
      isPlay: state => state.play.isPlay,
      musicList: state => state.play.musicList,
      currentSongId: state => state.play.currentSongId,
      currentIndex: state => state.play.currentIndex,
      currentMusicInfo: state => state.play.currentMusicInfo,
      saveVolume: state => state.play.saveVolume
    }),
    formatCurrentTime () {
      const time = Math.trunc(this.currentTime) * 1000
      function preToFixed (num, length = 2) {
        return (Array(length).join(0) + num).slice(-length)
      }
      const length = Math.floor(time / 1000)
      const minute = preToFixed(Math.floor(length / 60))
      const second = preToFixed(length - minute * 60)
      const result = `${minute}:${second}`
      return result
    }
  },
  watch: {
    // 根据vuex状态管理
    currentSongId: {
      async handler (val) {
        this.currentSong = this.musicList[this.currentIndex]
        await this.getSongUrlData(val)
        if (this.isPlay && this.$refs.audio.paused) {
          this.$refs.audio.play()
        }
      },
      immediate: true
    },
    // async currentSongId (val) {
    //   this.currentSong = this.musicList[this.currentIndex]
    //   await this.getSongUrlData(val)
    //   if (this.isPlay && this.$refs.audio.paused) {
    //     this.$refs.audio.play()
    //   }
    // },
    isPlay (val) {
      // 无歌曲直接返回
      console.log('isplay start')
      if (this.currentSongUrl === '') return null
      // 根据isPlay决定是否播放
      if (val) {
        this.$refs.audio.play()
      } else {
        this.$refs.audio.pause()
      }
    },
    volume (val) {
      this.$store.commit('setVolume', val)
    }
  },
  methods: {
    async getSongUrlData (id, level = 'standard') {
      const result = await getSongUrl(id, level)
      console.log(result)
      this.currentSongUrl = result.data[0].url
      this.info.song = this.currentSong.name
      this.info.singer = this.currentSong.ar[0].name
      this.totalTime = this.currentSong.dt
      this.$refs.audio.volume = this.saveVolume
      this.volume = this.saveVolume
    },
    updateCurrentTime () {
      this.currentTime = this.$refs.audio.currentTime
    },
    changeShowMusicList () {
      this.showMusicList = !this.showMusicList
    },
    changeCurrentTime (val) {
      this.$refs.audio.pause()
      this.$refs.audio.currentTime = val.target.value
      this.$refs.audio.play()
    }
  },
  mounted () {
    console.log(this.musicList, 'mounted  musiclist')
  }
}
</script>

<style lang="scss" scoped>

.footer{
  box-sizing: border-box;
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: space-between;
  left: 0;
  right:0;
  bottom: 0;
  height: 70px;
  border: 1px  solid #e0e0e0;
  &__info{
    display: flex;
    align-items: center;
    justify-content: left;
    width: 250px;
    &__left{
      margin: 0 12px;
      &__image{
        border-radius: 5px;
        width: 48px;
        height: 48px;
      }
    }
    &__right{
      display: flex;
      flex-direction: column;
      // align-items: flex-start;
      justify-content: center;
      font-size: 14px;
      color: #111111;
      &__song{
        &__name{
          margin-right: 10px;
        }
      }
    }
  }
  &__player{
    width: 500px;
    &__button{
      height: 35px;
      line-height: 35px;
      text-align: center;
      font-size: 12px;
      color: #313131;
      &__play{
        display: inline-block;
        height: 35px;
        width: 35px;
        line-height: 35px;
        text-align: center;
        font-size: 20px;
        border-radius: 50%;
        cursor: pointer;
      }
      &__play:hover{
        // 悬浮背景变灰
        background-color: #f5f5f5;
      }
      &__previous{
        display: inline-block;
        height: 35px;
        width: 35px;
        line-height: 35px;
        text-align: center;
        font-size: 18px;
        border-radius: 50%;
        cursor: pointer;
      }
      &__previous:hover{
        color:#f06767;
      }
      &__next{
        display: inline-block;
        height: 35px;
        width: 35px;
        line-height: 35px;
        text-align: center;
        font-size: 18px;
        border-radius: 50%;
        cursor: pointer;
      }
      &__next:hover{
        color:#f06767;
      }
    }
    &__progress{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 20px;
      &__item{
        margin: 0 10px;
        width: 350px;
      }
    }
  }
  &__menu{
    display: flex;
    justify-content: space-evenly;
    width: 200px;
    font-size: 24px;
    &__volume{
      position: relative;
      cursor: pointer;
      &__range{
        display: none;
        position: absolute;
        // top:-100px;
        left: 5px;
        transform: rotate(-90deg);
        transform-origin: left top;
        z-index: 50;
      }
    }
    &__volume:hover .footer__menu__volume__range{
      display: inline;
    }
    &__playlist{
      cursor: pointer;
    }
  }
}
</style>
