<template>
  <div class="songlist" >
    <table class="songlist__table">
      <colgroup class="songlist__table__col">
        <col class="songlist__table__col__1">
        <col class="songlist__table__col__2">
        <col class="songlist__table__col__3">
        <col class="songlist__table__col__4">
        <col class="songlist__table__col__5">
        <col class="songlist__table__col__6">
      </colgroup>
      <tr>
        <th><div class="songlist__table__cell"></div></th>
        <th><div class="songlist__table__cell">操作</div></th>
        <th><div class="songlist__table__cell">标题</div></th>
        <th><div class="songlist__table__cell">歌手</div></th>
        <th><div class="songlist__table__cell">专辑</div></th>
        <th><div class="songlist__table__cell">时间</div></th>
      </tr>
      <tr v-for="(item, index) in songList" :key="item.id"
      :class="{dark: index % 2 === 0, light: index % 2 === 1, row: true}"
      @dblclick="addMusic(item)">
        <td>
          <div class="songlist__table__cell">
            {{index + 1}}
          </div>
        </td>
        <td>
          <div class="songlist__table__cell iconfont songlist__table__col2">
            &#xe7df;
          </div>
        </td>
        <td>
          <div class="songlist__table__cell">
            {{item.name}}
          </div>
        </td>
        <td>
          <div class="songlist__table__cell">
            {{formatSinger(item.ar)}}
          </div>
        </td>
        <td>
          <div class="songlist__table__cell">
            {{item.al.name}}
          </div>
        </td>
        <td>
          <div class="songlist__table__cell">
            {{formatSongTime(item.dt)}}
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import { getTrackAll } from '../../api/api-playlist'
export default {
  name: 'MusicList',
  data () {
    return {
      id: this.$route.params.id,
      songList: []
    }
  },
  methods: {
    // 播放单曲
    addMusic (music) {
      this.$store.commit('playMusic', music)
    },
    // 获取歌曲列表
    async getData (id) {
      const result = await getTrackAll(id)
      this.songList = result.songs
    },
    // 格式化歌曲长度
    formatSongTime (time) {
      // 补足数字位数
      function preToFixed (num, length = 2) {
        return (Array(length).join(0) + num).slice(-length)
      }
      const length = Math.floor(time / 1000)
      const minute = preToFixed(Math.floor(length / 60))
      const second = preToFixed(length - minute * 60)
      const result = `${minute}:${second}`
      return result
    },
    // 格式化歌手名字
    formatSinger (ar) {
      let result = ''
      for (let i = 0; i < ar.length; i++) {
        result += ar[i].name
        if (i !== ar.length - 1) {
          result += '/'
        }
      }
      return result
    }
  },
  created () {
    // 执行
    this.getData(this.id)
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins.scss';
.songlist{
  &__table{
    font-size: 12px;
    &__cell{
      width: auto;
      max-width: 100%;
      height: 35px;
      line-height: 35px;
      word-break: break-all;
      overflow: hidden;
    }
    &__col{
      &__1{
        text-align: center;
        width: 55px;
      }
      &__2{
        width: 55px;
      }
      &__3{
        width: 300px;
      }
      &__4{
        width: 140px;
      }
      &__5{
        width: 190px;
      }
      &__6{
        width: 70px;
      }
    }
  }
}
.row{
  height: 35px;
  line-height: 35px;
}
td{
  height: 35px;
  line-height: 35px;
}
.dark{
  background-color: #fafafa;
  color: #222222;
  @include test;
}
.light{
  background-color: #ffffff;
  @include test;
}
</style>
