import axios from 'axios'
// 基础配置
axios.defaults.headers.post['Content-Type'] = 'application/json'
const instance = axios.create({
  baseURL: 'https://netease-clound-music-api-zeta.vercel.app/',
  timeout: 20000,
  withCredentials: true
})
// get方法
// const get = (url, params = {}) => {
//   return new Promise((resolve, reject) => {
//     console.log('get start')
//     instance.get(url, { params }).then(response => { console.log('start'); resolve(response) }, error => { console.log('end'); reject(error) })
//   })
// }
const get = (url, params = {}) => {
  return new Promise((resolve, reject) => {
    instance.get(url, { params }).then(
      (response) => {
        resolve(response)
      },
      (error) => {
        reject(error)
      }
    )
  })
}

export { get }
