<template>
  <!-- 最近播放 -->
  <div class="recentsong">
    <!-- 头部信息 -->
    <div class="recentsong__head">
      <div class="recentsong__head__title">
        <div class="recentsong__head__title__left">
          <span class="recentsong__head__title__left__name">最近播放</span>
          <span class="recentsong__head__title__left__count">共 {{total}} 首</span>
        </div>
        <div class="recentsong__head__title__right" @click="clearList()">清空列表</div>
      </div>
      <PlayAllButtonVue :songList="songList"></PlayAllButtonVue>
    </div>
    <!-- 歌曲列表 -->
    <div class="recentsong__list">
      <table class="recentsong__list__table">
      <colgroup class="recentsong__list__table__col">
        <col class="recentsong__list__table__col__1">
        <col class="recentsong__list__table__col__3">
        <col class="recentsong__list__table__col__4">
        <col class="recentsong__list__table__col__6">
      </colgroup>
      <tr>
        <th><div class="recentsong__list__table__cell"></div></th>
        <th><div class="recentsong__list__table__cell">音乐标题</div></th>
        <th><div class="recentsong__list__table__cell">歌手</div></th>
        <th><div class="recentsong__list__table__cell">时间</div></th>
      </tr>
      <tr v-for="(item, index) in recentSongList" :key="item.data.id"
      :class="{dark: index % 2 === 0, light: index % 2 === 1, row: true}"
      @dblclick="addMusic(item)">
        <td>
          <div class="recentsong__list__table__cell">
            {{index + 1}}
          </div>
        </td>
        <td>
          <div class="recentsong__list__table__cell">
            {{item.data.name}}
          </div>
        </td>
        <td>
          <div class="recentsong__list__table__cell">
            {{formatSinger(item.data.ar)}}
          </div>
        </td>
        <td>
          <div class="recentsong__list__table__cell">
            {{ lateTime(nowTime-item.playTime)}}
          </div>
        </td>
      </tr>
    </table>
    </div>
  </div>
</template>

<script>
import { getRecentSong } from '../api/api_record'
import PlayAllButtonVue from '../components/button/PlayAllButton.vue'
export default {
  name: 'RecentSong',
  components: { PlayAllButtonVue },
  data () {
    return {
      recentSongList: [],
      songList: [],
      total: 0,
      nowTime: Date.now()
    }
  },
  methods: {
    async getData () {
      const result = await getRecentSong()
      this.recentSongList = result.data.list
      this.songList = this.recentSongList.map(item => item.data)
      this.total = result.data.total
    },
    addMusic (music) {
      this.$store.commit('playMusic', music)
    },
    lateTime (span) {
      span = Math.floor(span / 1000)
      if (span < 1) {
        return '刚刚'
      } else if (span < 60) {
        return `${span}分钟前`
      } else {
        span = Math.floor(span / 60)
      }
      if (span < 24) {
        return `${span}小时前`
      } else {
        span = Math.floor(span / 24)
        return `${span}天前`
      }
    },
    clearList () {
      this.songList = []
      this.total = 0
    },
    formatSinger (ar) {
      let result = ''
      for (let i = 0; i < ar.length; i++) {
        result += ar[i].name
        if (i !== ar.length - 1) {
          result += '/'
        }
      }
      return result
    }
  },
  created () {
    this.getData()
    console.log(this.total, 'total')
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/styles/mixins.scss';
.recentsong{
  padding: 20px;
  &__head{
    margin-bottom: 10px;
    &__title{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      &__left{
        &__name{
          margin-right: 20px;
          font-size: 20px;
          color: #111111;
          font-weight: 600;
        }
        &__count{
          font-size: 12px;
          color: #e1e1e1;
        }
      }
      &__right{
        font-size: 16px;
        color: #0b58b0;
        cursor: pointer;
      }
    }
      &__play{
          height: 32px;
          width: 150px;
          border-radius: 16px;
          color:white;
          background-color: #ec4141;
          line-height: 32px;
          margin-right: 10px;
          cursor: pointer;
          &__left{
            display: inline-block;
            height: 32px;
            border-radius:16px  0 0 16px;
            border-right: 1px solid rgba(#ffffff,0.5);
            padding:0 5px 0 16px;
          }
          &__right{
            display: inline-block;
            height: 32px;
            border-radius:  0 16px 16px 0;
            padding: 0 10px 0;
          }
      }
      &__play:hover{
          background-color: #d73535;
          &__left:hover{
            background-color: #cc3232;
          }
          &__right:hover{
            background-color: #cc3232;
          }
      }
  }
}

.recentsong__list{
  width: 100%;
  color: #666666;
  &__table{
    width: 100%;
    font-size: 12px;
    &__cell{
      width: auto;
      max-width: 100%;
      height: 35px;
      line-height: 35px;
      word-break: break-all;
      overflow: hidden;
      text-align: left;
    }
    &__col{
      &__1{
        text-align: center;
        width: 55px;
        color: #e1e1e1;
      }
      &__3{
        width: calc(100% - 295px);
      }
      &__4{
        width: 140px;
      }
      &__6{
        width: 100px;
      }
    }
  }
}
.row{
  height: 35px;
  line-height: 35px;
}
td{
  height: 35px;
  line-height: 35px;
}
.dark{
  background-color: #fafafa;
  color: #222222;
  @include test;
}
.light{
  background-color: #ffffff;
  @include test;
}
</style>
