<template>
  <div class="container">
    <div class="container__desc"
    v-for="item in desc"
    :key="item.ti">
      <div class="container__desc__ti" >
        {{item.ti}}
      </div>
      <div class="container__desc__text" >
      {{ item.txt }}
      </div>
    </div>
  </div>
</template>

<script>
import { getDesc } from '../../api/api-singer'

export default {
  name: 'SingerDesc',
  data () {
    return {
      desc: [],
      briefDesc: ''
    }
  },
  methods: {
    async getData () {
      const result = await getDesc(this.$route.params.id)
      this.desc = result.introduction
      this.briefDesc = result.briefDesc
      console.log(result)
      console.log(this.desc)
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.desc{
  width: 100%;
}
.container{
  width: 100%;
  // &__brief{
  //   white-space: pre-wrap;
  //   text-indent: 30px;
  //   font-size: 14px;
  //   font-weight: 300;
  // }
  &__desc__ti{
    margin-bottom: 20px;
    font-size: 16px;
    color: #111111;
    line-height: 2;
    font-weight: 600;
  }
  &__desc__text{
    margin-bottom: 40px;
    line-height: 2;
    font-size: 14px;
    white-space: pre-wrap;
  }
}
</style>
