<template>
  <div class="cover" @click="changeRouter()">
    <div class="cover__swap">
      <img :src="url" alt="" class="cover__swap__image">
    </div>
    <div class="cover__name">
      {{ name }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'SingerCover',
  props: ['name', 'url', 'id'],
  methods: {
    changeRouter () {
      console.log(this.id, 'cover id')
      this.$router.push({ name: 'SingerDetail', params: { id: this.id } })
    }
  }
}
</script>

<style lang="scss" scoped>
.cover{
  &__swap{
    &__image{
      width: 100%;
      aspect-ratio: 1 / 1;
    }
  }
  &__name{
    font-size: 14px;
    color: #111111;
    line-height: 2;
  }
}
</style>
