<template>
  <!-- 发现音乐 -> 歌单 -> 头部精品歌单 -->
  <div class="banner"  @click="pushRouter()">
    <div class="banner__cover">
      <img :src="coverImgUrl" alt="" class="banner__cover__image">
      <span class="banner__cover__playcount iconfont">&#xe718;{{ changePlayCount(playCount) }}</span>
    </div>
    <div class="banner__title">
      <div class="banner__title__name">{{ name }}</div>
      <div class="banner__title__creator">by {{ nickname}}</div>
      <div class="banner__title__writer">{{ copywriter}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CatBanner',
  data () {
    return {
      banner: {}
    }
  },
  props: ['name', 'playCount', 'coverImgUrl', 'copywriter', 'nickname', 'id'],
  methods: {
    pushRouter () {
      this.$router.push({ name: 'PlayList', params: { id: this.id } })
    },
    changePlayCount (count) {
      let result
      if (!count) { return null }
      if (count >= 100000000) {
        result = Math.floor(count / 100000000)
        result = `${result}亿`
      } else if (count >= 100000) {
        result = Math.floor(count / 10000)
        result = `${result}万`
      } else {
        return count
      }
      return result
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../../assets/styles/mixins.scss';
.banner{
  display: flex;
  height: 170px;
  border-radius: 5px;
  &__cover{
    position: relative;
    &__playcount{
      position: absolute;
      right: 10px;
      top: 5px;
      color: white;
      font-size: 14px;
    }
    &__image{
      width: 140px;
      height: 140px;
      border-radius: 5px;
    }
  }
  &__title{
    flex: 1;
    margin-left: 10px;
    &__name{
      margin-top: 25px;
      font-size: 14px;
      color: #1f1f1f;
      width: 200px;
      @include ellipsis;
    }
    &__creator{
      margin-top: 15px;
      font-size: 12px;
      color: #85848d;
    }
    &__writer{
      margin-top: 20px;
      font-size: 12px;
      color: #a1a1a1;
    }
  }
}
</style>
