<template>
  <!-- 推荐歌单 -->
  <div class="recommend" >
  <!-- 歌单表格 -->
    <div class="recommend__item" v-if="isLogin">
      <DailySongsCover></DailySongsCover>
    </div>
    <div class="recommend__item" v-for="(item, index) in playlists" :key="index">
      <PlayListCover :id="item.id" :title="item.name" :imgabc="item.picUrl" :play-count="item.playcount"></PlayListCover>
    </div>
  </div>
</template>
<script>
import DailySongsCover from '../../../components/PlayList/DailySongsCover.vue'
import PlayListCover from '../../../components/PlayList/PlayListCover.vue'
import { getRecommendList, getPersonalized } from '../../../api/api-playlist.js'
export default {
  name: 'RecommendPlayList',
  components: { DailySongsCover, PlayListCover },
  data () {
    return {
      playlists: [],
      isLogin: false
    }
  },
  methods: {
    // 获取歌单数据
    async getData () {
      console.log('start GetData')
      let result = await getRecommendList()
      console.log(result)
      if (result.code === 200) {
        this.playlists = result.recommend
      }
      result = await getPersonalized()
      if (result.code === 200) {
        this.playlists = this.playlists.concat(result.result)
      }
      if (this.playlists.length > 10) {
        this.playlists.length = 10
      }
    }
  },
  created () {
    this.getData()
    console.log(this.playlists, 'playlists')
  }
}
</script>
<style lang="scss">
.recommend{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 18px;
}
</style>
