<template>
  <!-- 歌单详情面 -->
  <div class="detail">
    <!-- 头部歌单信息 -->
    <div class="detail__head">
      <!-- 左边图片 -->
      <div class="detail__head__left">
        <img :src="singer.artist.cover" alt="" class="detail__head__left__cover">
      </div>
      <!-- 右边信息 -->
      <div class="detail__head__right">
        <!-- 姓名    -->
        <div class="detail__head__right__name">{{ singer.artist.name }}</div>
        <!-- 操作   -->
        <div class="detail__head__right__collect iconfont">
          &#xe62a;收藏
        </div>
        <!-- 歌曲,mv,专辑数量  -->
        <div class="detail__head__right__count">
          <span class="detail__head__right__count__music">单曲数：{{ singer.artist.musicSize }}</span>
          <span class="detail__head__right__count__album">专辑数：{{ singer.artist.albumSize }}</span>
          <span class="detail__head__right__count__mv">MV数：{{ singer.artist.mvSize}}</span>
        </div>
      </div>
    </div>
    <!-- 内容 -->
    <div class="detail__body">
      <!-- 路由 分别导向  专辑 MV 歌手详情 相似歌手 -->
      <div class="detail__body__router">
        <router-link
        :class="{detail__body__router__item:true, 'detail__body__router__item-active':item.id === currentId}"
        @click="handleChangeRouter(item.id)"
        v-for="item in routers"
        :key="item.id"
        :to="`/SingerDetail/${id}/${item.router}`">
          {{item.name}}
        </router-link>
      </div>
      <!-- 路由显示 -->
      <div class="detail__body__view">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import { getSingerDetail } from '../../api/api-singer'

export default {
  name: 'SingerDetail',
  data () {
    return {
      // 歌手id
      id: this.$route.params.id,
      singer: {},
      currentId: 1,
      routers: [
        {
          id: 1,
          name: '专辑',
          router: 'AlbumList'
        },
        {
          id: 2,
          name: 'MV',
          router: 'MVList'
        },
        {
          id: 3,
          name: '歌手详情',
          router: 'SingerDesc'
        },
        {
          id: 4,
          name: '相似歌手',
          router: 'SimiSinger'
        }
      ]
    }
  },
  methods: {
    async getData () {
      const result = await getSingerDetail(this.id)
      console.log('result', this.result)
      this.singer = result.data
      console.log(this.id, this.result)
    },
    handleChangeRouter (id) {
      this.currentId = id
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.detail{
  padding:30px;
  &__head{
    display: flex;
    margin-bottom: 40px;
    &__left{
      width: 184px;
      height: 184px;
      margin-right: 20px;
      &__cover{
        width: 100%;
        height: 100%;
      }
    }
    &__right{
      flex:1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      &__name{
        margin-bottom: 20px;
        font-size: 30px;
        font-weight: 600;
        color: #111111;
      }
      &__collect{
        margin-bottom: 20px;
          height: 32px;
          border: 1px solid #d9d9d9;
          border-radius: 16px;
          padding: 0 16px;
          line-height: 32px;
          font-size: 14px;
          cursor: pointer;
      }
      &__collect:hover{
          background-color: #f2f2f2;
        }
      &__count{
        font-size: 12px;
        color:#676767;
        &__music{
          margin-right: 30px;
        }
        &__album{
          margin-right: 30px;
        }
      }
     }
  }
  &__body{
    &__router{
      display: flex;
      align-items: center;
      margin-bottom: 40px;
      &__item{
        position: relative;
        font-size: 16px;
        font-weight: 300px;
        margin-right: 20px;
        color: #111111;
        &-active{
          font-size: 18px;
          font-weight: 700;
        }
        &-active::after{
          display: block;
          content: '';
          margin: 0 auto;
          width: 90%;
          height: 4px;
          border-radius: 2px;
          background-color: #ec4141;
        }
      }
    }
  }
}
</style>
