<template>
  <!-- 当前播放列表，平时隐藏 -->
  <div class="currentlist">
      <div class="currentlist__head">
        <div class="currentlist__head__title">当前播放</div>
        <div class="currentlist__head__info">
          <div class="currentlist__head__info__count">总{{ songCount }}首</div>
          <div class="currentlist__head__info__operate">
            <span class="currentlist__head__info__operate__collect iconfont">&#xe62a;收藏全部</span>
            <span class="currentlist__head__info__operate__empty">清空列表</span>
          </div>
        </div>
      </div>
      <div class="songlist" >
        <table class="songlist__table">
          <colgroup class="songlist__table__col">
            <col class="songlist__table__col__3">
            <col class="songlist__table__col__4">
            <col class="songlist__table__col__6">
          </colgroup>
          <tr>
            <th><div class="songlist__table__cell">标题</div></th>
            <th><div class="songlist__table__cell">歌手</div></th>
            <th><div class="songlist__table__cell">时间</div></th>
          </tr>
          <tr v-for="(item, index) in songList" :key="item.id"
          :class="{dark: index % 2 === 0, light: index % 2 === 1, row: true}"
          @dblclick="addMusic(item)">
            <td>
              <div class="songlist__table__cell">
                {{item.name}}
              </div>
            </td>
            <td>
              <div class="songlist__table__cell">
                {{formatSinger(item.ar)}}
              </div>
            </td>
            <td>
              <div class="songlist__table__cell">
                {{formatSongTime(item.dt)}}
              </div>
            </td>
          </tr>
        </table>
      </div>
    </div>
</template>

<script>
import { useStore } from 'vuex'

const useMusicInfoEffect = () => {
  const store = useStore()
  const songList = store.state.play.musicList
  const songCount = songList.length
  return { songList, songCount }
}
export default {
  name: 'CurrentPlayList',
  setup () {
    const { songList, songCount } = useMusicInfoEffect()
    return { songList, songCount }
  },
  methods: {
    // 播放单曲
    addMusic (music) {
      this.$store.commit('playMusic', music)
    },
    // 格式化歌曲长度
    formatSongTime (time) {
      // 补足数字位数
      function preToFixed (num, length = 2) {
        return (Array(length).join(0) + num).slice(-length)
      }
      const length = Math.floor(time / 1000)
      const minute = preToFixed(Math.floor(length / 60))
      const second = preToFixed(length - minute * 60)
      const result = `${minute}:${second}`
      return result
    },
    // 格式化歌手名字
    formatSinger (ar) {
      let result = ''
      for (let i = 0; i < ar.length; i++) {
        result += ar[i].name
        if (i !== ar.length - 1) {
          result += '/'
        }
      }
      return result
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/styles/mixins.scss';
.currentlist{
    position: absolute;
    border-left: 1px solid #f1f1f1;
    right: 0;
    top: 60px;
    bottom: 70px;
    width: 420px;
    background-color:  #f5f5f5;
    z-index: 20;
    &__head{
      padding: 20px 10px 10px;
      margin-bottom: 10px;
      background-color: white;
      &__title{
        margin-bottom: 10px;
        font-size: 20px;
        color: #111111;
        font-weight: 600;
      }
      &__info{
        display: flex;
        align-items: center;
        justify-content: space-between;
        &__count{
          font-size: 12px;
          color: #e1e1e1;
        }
        &__operate{
          font-size: 14px;
          color: #222222;
          &__collect{
            cursor: pointer;
          }
          &__empty{
            margin-left: 20px;
            color: aqua;
            cursor: pointer;
          }
        }
      }
    }
  }
.songlist{
  height: calc(100% - 95px);
  overflow-y: auto;
  background-color: white;
  &__table{
    font-size: 12px;
    &__cell{
      width: auto;
      max-width: 100%;
      height: 35px;
      line-height: 35px;
      word-break: break-all;
      overflow: hidden;
    }
    &__col{
      &__3{
        width: 200px;
      }
      &__4{
        width: 140px;
      }
      &__6{
        width: 70px;
      }
    }
  }
}
.row{
  height: 35px;
  line-height: 35px;
}
td{
  height: 35px;
  line-height: 35px;
}
.dark{
  background-color: #fafafa;
  color: #222222;
  @include test;
}
.light{
  background-color: #ffffff;
  @include test;
}
</style>
