<template>
  <!-- 发现音乐 -> 个性推荐 -> 轮播图 -->
  <div class="banner" @mouseover="overStop" @mouseout="outStart">
    <div class="banner__swap">
      <!-- 所有图片 -->
      <ul class="banner__swap__table">
        <li v-for="(item, index) in banners"
        :key="index"
        :class="{banner__swap__table__item: Math.abs(index - showIndex) > 1,
        'banner__swap__table__left': index === showIndex-1 ,
        'banner__swap__table__right': index === showIndex+1,
        'banner__swap__table__show': index === showIndex
        }"
        @click="handleBanner(item)">
          <img :src="item.imageUrl" alt="" class="banner__swap__table__item__image">
        </li>
      </ul>
      <!--  左右箭头 -->
      <div class="banner__swap__previous iconfont"
      @click="handleIndex(-1)">&#xe7eb;</div>
      <div class="banner__swap__next iconfont"
      @click="handleIndex(1)">&#xe6a3;</div>
    </div>
    <!-- 底部滑块 -->
    <div class="banner__slider" v-if="banners.length > 2">
      <div v-for="index in banners.length - 2"
      :class="{ banner__slider__item:true,'banner__slider__item-active': index === showIndex}"
      :key="index"
      @mousemove="changeIndex(index)"></div>
    </div>
  </div>
</template>
<script>
import { getBanner } from '../../../api/api-banner'
import { getSongDetail } from '../../../api/api_other'
export default {
  name: 'Banner',
  data () {
    return {
      banners: [],
      showIndex: 1,
      timeOut: null
    }
  },
  watch: {
    showIndex (val) {
      if (val < 1) {
        this.showIndex = this.banners.length - 2
      } else if (val > this.banners.length - 2) {
        this.showIndex = 1
      }
    }
  },
  methods: {
    async getBannerData () {
      const result = await getBanner()
      console.log(result.banners)
      this.banners = result.banners
      this.banners.push(result.banners[0])
      this.banners.unshift(result.banners[result.banners.length - 2])
      console.log(this.banners)
    },
    // 开始轮播
    startBanner () {
      this.timeOut = setInterval(() => {
        this.showIndex++
      }, 5000)
    },
    // 鼠标停留停止计时
    overStop () {
      clearInterval(this.timeOut)
    },
    // 鼠标离开重新计时
    outStart () {
      this.timeOut = setInterval(() => {
        this.showIndex++
      }, 5000)
    },
    changeIndex (index) {
      this.showIndex = index
    },
    handleIndex (val) {
      this.showIndex += val
    },
    async playMusic (item) {
      const result = await getSongDetail(item.targetId)
      const song = result.data.songs[0]
      this.$store.commit('playMusic', song)
    },
    handleBanner (item) {
      switch (item.typeTitle) {
        case '歌单':this.$router.push(`/PlayList/${item.targetId}`)
          break
        case '新歌首发':this.playMusic(item); break
        case '新碟首发': break
        case 'MV推荐':break
        case '独家策划':window.open(item.url); break
        default:break
      }
    }
  },
  created () {
    this.getBannerData()
    this.startBanner()
  }
}
</script>
<style lang="scss" scoped>
.banner{
  position: relative;
  margin-bottom: 20px;
  height: 230px;
  &__swap{
    position: relative;
    &__previous{
      display: none;
      position: absolute;
      left: 30px;
      top: 80px;
      height: 40px;
      width: 40px;
      border-radius: 50%;
      line-height: 40px;
      font-size: 24px;
      color: white;
      text-align: center;
      background-color: rgba($color: #000000, $alpha: 0.1);
      cursor: pointer;
      z-index: 5;
    }
    &__next{
      display: none;
      position: absolute;
      right: 30px;
      top: 80px;
      height: 40px;
      width: 40px;
      border-radius: 50%;
      line-height: 40px;
      font-size: 24px;
      color: white;
      text-align: center;
      background-color: rgba($color: #000000, $alpha: 0.1);
      cursor: pointer;
      z-index: 5;
    }
    &__table{
      position: relative;
      display: block;
      width: 100%;
      height: 200px;
      &__item{
        display: block;
        position: absolute;
        left: 50%;
        top: 40px;
        transform: translateX(-50%);
        transition: all 1000ms  ;
        width: 432px;
        height: 160px;
        z-index: 1;

        &__image{
          border-radius: 5px;
          width: 100%;
          height: 100%;
        }
      }
      &__left{
        display: block;
        position: absolute;
        width: 432px;
        height: 160px;
        left: 0;
        top: 20px;
        transform: none;
        z-index: 2;
        transition: all  1000ms ;
      }
      &__right{
        display: block;
        position: absolute;
        width: 432px;
        height: 160px;
        right: 0;
        top: 20px;
        left: unset;
        transform: none;
        z-index: 2;
        transition: all  1000ms ;
      }
      &__show{
        display: block;
        position: absolute;
        width: 540px;
        height: 200px;
        left: 50%;
        top:0;
        transform: translateX(-50%);
        transition: all 1000ms ;
        z-index: 3;
      }
    }
  }
  &__slider{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    &__item{
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background-color: #afafaf;
      margin-right: 15px;
      &-active{
        background-color: #fa0000;
      }
    }
  }
  &__swap:hover>.banner__swap__previous{
    display: block;
  }
  &__swap:hover>.banner__swap__next{
    display: block;
  }
}
</style>
