<template>
  <!-- 发现音乐 -> 歌单 -->
  <div class="catlist">
    <!-- 精品歌单 -->
    <div class="catlist__high" >
      <CatBanner :cat = 'currentCat'></CatBanner>
    </div>
    <!-- 头部分类 -->
    <div class="catlist__head">
      <!-- 左边所有分类 -->
      <div class="catlist__head__left iconfont" @click="changeCatShow()">
        {{currentCat}}&#xe6a3;
        <div class="catlist__head__left__all" v-show="catShow">
          <div class="catlist__head__left__all__item" @click="changeCurrentCat('全部歌单')">全部歌单</div>
          <div v-for="(item, key) in categories" :key="key"
          class="catlist__head__left__all__cat">
            <div class="catlist__head__left__all__cat__left">
              {{item}}
            </div >
            <div class="catlist__head__left__all__cat__right">
            <template v-for="cat in catLists"
              :key="cat.name"
              >
              <div class="catlist__head__left__all__cat__right__item"
              v-if="cat.category == key"
              @click="changeCurrentCat(cat.name)"
              >{{cat.name}}</div>
            </template>
            </div>
          </div>
        </div>
      </div>
      <!-- 右边热门分类 -->
      <div class="catlist__head__right">
        <div v-for="item in hotLists" :key="item.id"
        @click="changeCurrentCat(item.playlistTag.name)"
        :class="{catlist__head__right__item: true, 'catlist__head__right__item-active': item.playlistTag.name === currentCat}">
          {{ item.playlistTag.name }}
        </div>
      </div>
    </div>
    <div class="catlist__body">
      <div class="catlist__body__item"
      v-for="item in playList"
      :key="item.id">
        <PlayListCover :id="item.id"
        :playCount="item.playCount"
        :title="item.name"
        :imgabc="item.coverImgUrl"></PlayListCover>
      </div>
    </div>
  </div>
</template>

<script>
import { getCatList, getHotCatList, getTopPlayList } from '../../../api/api-playlist'
import PlayListCover from '../../../components/PlayList/PlayListCover.vue'
import CatBanner from '../catList/CatBanner.vue'

export default {
  name: 'CatList',
  components: { PlayListCover, CatBanner },
  data () {
    return {
      // 当前分类
      currentCat: '华语',
      // 所有分类
      catLists: [],
      // 热门分类
      hotLists: [],
      // 分类的类别
      categories: {},
      catShow: false,
      playList: []
    }
  },
  watch: {
    currentCat: {
      async handler (val) {
        const result = await getTopPlayList(this.currentCat)
        this.playList = result.playlists
      },
      immediate: true
    }
  },
  methods: {
    async getData () {
      // 获取所有分类
      let result = await getCatList()
      this.catLists = result.sub
      this.categories = result.categories
      result = await getHotCatList()
      // 获取热门分类
      this.hotLists = result.tags
      console.log(this.catgories, 'cat')
    },
    changeCurrentCat (val) {
      this.currentCat = val
    },
    changeCatShow () {
      this.catShow = !this.catShow
    }
  },
  created () {
    // 获取数据
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.catlist{
  &__high{
    margin-bottom: 20px;
  }
  &__head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    &__left{
      position: relative;
      height: 30px;
      line-height: 30px;
      padding: 0 15px;
      border: 1px solid #d9d9d9;
      border-radius: 15px;
      font-size: 14px;
      color: #1f1f1f;
      cursor: pointer;
      &__all{
        position: absolute;
        top: 45px;
        left: -15px;
        width: 746px;
        border: 1px solid #ebebeb;
        border-radius: 5px;
        z-index: 2;
        background-color: white;
        &__item{
          height: 55px;
          line-height: 55px;
          padding:0 30px;
          border-bottom: 1px solid #ebebeb;
          font-size: 12px;
          cursor: pointer;
        }
        &__item:hover{
          color: #fa0000;
        }
        &__cat{
          display: flex;
          padding: 20px;
          &__left{
            color: #d9d9d9;
            margin-right: 60px;
          }
          &__right{
            flex:1;
            display: grid;
            grid-template-columns: repeat(6, 1fr);
            &__item:hover{
              color: #fa0000;
            }
          }
        }
      }
    }
    &__left:hover{
      background-color: #f2f2f2;
    }
    &__right{
      display: flex;
      align-items: center;
      &__item{
        margin-right: 10px;
        font-size: 12px;
        color: #7f7f7f;
        cursor: pointer;
      }
      &__item:hover{
        color: #4f4f4f;
      }
      &__item-active{
        color: #ff0000;
      }
    }
  }
  &__body{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 20px;
  }
}
</style>
