<template>
  <div class="subscriber">
    <div class="subscriber__item"
    v-for="item in subscribers"
    :key="item.userId">
      <div class="subscriber__item__left">
        <img :src="item.avatarUrl" alt="" class="subscriber__item__left__image">
      </div>
      <div class="subscriber__item__right">
        <div class="subscriber__item__right__nickname">{{item.nickname}}</div>
        <div class="subscriber__item__right__signature">{{item.signature}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSubscriber } from '../../api/api-playlist'

export default {
  name: 'SubscriberList',
  data () {
    return {
      id: this.$route.params.id,
      subscribers: []
    }
  },
  methods: {
    async getData () {
      const result = await getSubscriber(this.id)
      this.subscribers = result.subscribers
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.subscriber{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  padding: 20px;
  &__item{
    display: flex;
    align-items: center;
    &__left{
      margin-right: 10px;
      font-size: 16px;
      &__image{
        width: 100px;
        height: 100px;
        border-radius: 50px;
      }
    }
    &__right{
      flex:1;
      &__nickname{
        margin-bottom: 10px;
        font-size: 16px;
      }
      &__signature{
        font-size: 14px;
      }
    }
  }
}
</style>
