<template>
  <!-- 歌单封面 -->
  <div class="playlist"  @click="goRoute(id)">
    <!-- 封面部分 -->
    <div class="playlist__cover">
      <!-- 封面图片 -->
      <img :src="imgabc" alt="" class="playlist__cover__img">
      <!-- 右上角播放量 -->
      <span class="playlist__cover__count iconfont">&#xe718;{{changePlayCount(playCount)}}</span>
      <!-- 右下角播放图标 -->
      <div class="playlist__cover__play iconfont">&#xe717;</div>
    </div>
    <!-- 歌单标题 -->
    <div class="playlist__title">{{title}}</div>
  </div>
</template>

<script>
export default {
  name: 'PlayListCover',
  props: ['id', 'playCount', 'title', 'imgabc'],
  methods: {
    // 将播放量单位改成万，忆
    changePlayCount (count) {
      let result
      if (!count) { return null }
      if (count >= 100000000) {
        result = Math.floor(count / 100000000)
        result = `${result}亿`
      } else if (count >= 100000) {
        result = Math.floor(count / 10000)
        result = `${result}万`
      } else {
        return count
      }
      return result
    },
    goRoute (route) {
      console.log(route, 'route')
      this.$router.push(`/PlayList/${route}`)
    }
  }
}
</script>

<style lang="scss" scoped>
.playlist{
  min-height: 184px;
  min-width: 138px;
  margin-bottom: 20px;
  cursor: pointer;
  &__cover{
    position: relative;
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    &__img{
      border-radius: 5px;
      width: 100%;
      height: 100%;
    }
    &__count{
      position: absolute;
      top: 5px;
      right: 5px;
      font-size: 12px;
      color: white;
    }
    &__play{
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    font-size: 12px;
    line-height: 30px;
    text-align: center;
    color:red;
    background-color: white;
    transition: all 1000ms ease-in;
    cursor: pointer;
    opacity: 0;
    }
    &:hover .playlist__cover__play{
      opacity: 1;
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    font-size: 12px;
    line-height: 30px;
    text-align: center;
    color:red;
    background-color: white;
    }
  }
  &__title{
    height: 32px;
    line-height: 16px;
    font-size: 12px;
    color: #111111;
    text-align: left;
  }
}
</style>
