<template>
  <!-- 发现音乐 -> 歌手页面 -->
  <div class="singer">
    <!-- 头部筛选部分 -->
    <div class="singer__filter">
      <div class="singer__filter__area">
        <div class="singer__filter__area__left">语种：</div>
        <div class="singer__filter__area__right">
          <div :class="{ singer__filter__area__right__item:true, 'singer__filter__area__right__item-active': item.id == currentArea }"
          v-for="item in area"
          :key="item.name">
            {{ item.name }}
          </div>
        </div>
      </div>
      <div class="singer__filter__type">
        <div class="singer__filter__type__left">分类：</div>
        <div class="singer__filter__type__right">
          <div :class="{ singer__filter__type__right__item:true, 'singer__filter__type__right__item-active': item.id == currentType }"
            v-for="item in type"
            :key="item.name">
              {{ item.name }}
            </div>
        </div>
      </div>
    </div>
    <div class="singer__swap">
      <div class="singer__swap__item"
      v-for="item in singers"
      :key="item.id">
        <SingerCover :name = "item.name" :url = "item.picUrl" :id="item.id"></SingerCover>
      </div>
    </div>
  </div>
</template>

<script>
import SingerCover from '../singer/SingerCover.vue'
import { getSinger } from '../../../api/api-singer'
export default {
  name: 'Singer',
  components: { SingerCover },
  data () {
    return {
      singers: [],
      area: [
        {
          name: '全部',
          id: -1
        },
        {
          name: '华语',
          id: 7
        },
        {
          name: '日本',
          id: 8
        },
        {
          name: '欧美',
          id: 96
        },
        {
          name: '韩国',
          id: 16
        },
        {
          name: '其他',
          id: 0
        }
      ],
      type: [
        {
          name: '全部',
          id: -1
        },
        {
          name: '男歌手',
          id: 1
        },
        {
          name: '女歌手',
          id: 2
        },
        {
          name: '乐队组合',
          id: 3
        }
      ],
      currentArea: -1,
      currentType: -1
    }
  },
  methods: {
    async getData () {
      const result = await getSinger(this.currentArea, this.currentType)
      this.singers = result.artists
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.singer{
  &__filter{
    margin-bottom: 20px;
    &__area{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      &__left{
        margin-right: 40px;
        font-size: 12px;
        color: #5f5f5f;
      }
      &__right{
        flex: 1;
        display: flex;
        align-items: center;
        &__item{
          margin-right: 30px;
          padding: 0 6px;
          font-size: 12px;
          color: #7a7a7a;
          cursor: pointer;
        &__item:hover{
          color: #5f5f5f;
        }
          &-active{
            color: #fa0000;
            background-color: #feeeee;
            border-radius: 6px;
          }
        }
      }
    }
    &__type{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &__left{
        margin-right: 40px;
        font-size: 12px;
        color: #5f5f5f;
      }
      &__right{
        flex: 1;
        display: flex;
        align-items: center;
        &__item{
          margin-right: 30px;
          padding: 0 6px;
          font-size: 12px;
          color: #7a7a7a;
          cursor: pointer;
        &__item:hover{
          color: #5f5f5f;
        }
          &-active{
            color: #fa0000;
            background-color: #feeeee;
            border-radius: 5px;
          }
        }
      }
    }
  }
  &__swap{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
  }
}
</style>
