<template>
  <!-- 中间内容区 -->
  <div class="content">
    <!-- 嵌套路由 -->
    <div class="content__menu">
      <router-link @click="handleMenuItemChange(item.id)"
      :class="{content__menu__item:true,'content__menu__item-active':item.id === currentItemId}"
      v-for="item in routerList"
      :key="item.id"
      :to='item.route'>
        {{item.text}}
      </router-link>
    </div>
    <!-- 路由视图 -->
    <div class="content__view" >
      <router-view @go-router="goRouter(2)"></router-view>
    </div>
  </div>
</template>
<script>
import { useRouter } from 'vue-router'
import { ref } from 'vue'
// 路由逻辑
const useRouterEffect = () => {
  const router = useRouter()
  // 当前点击的页面的id
  const currentItemId = ref(1)
  const handleMenuItemChange = (id) => {
    currentItemId.value = id
  }
  const goRouter = (id) => {
    router.push('/CatList')
    handleMenuItemChange(2)
  }
  // 路由集合
  const routerList = [
    {
      id: 1,
      text: '个性推荐',
      route: 'Recommend'
    },
    {
      id: 2,
      text: '歌单',
      route: '/CatList'
    },
    {
      id: 3,
      text: '排行榜',
      route: '/Chart'
    },
    {
      id: 4,
      text: '歌手',
      route: '/Singer'
    }
  ]
  return { routerList, currentItemId, handleMenuItemChange, goRouter }
}
export default {
  name: 'Content',
  setup () {
    const { routerList, currentItemId, handleMenuItemChange, goRouter } = useRouterEffect()
    return { routerList, currentItemId, handleMenuItemChange, goRouter }
  }
}
</script>
<style lang="scss" scoped>
.content{
  overflow: auto;
  width: 100%;
  min-width: 822px;
  height: 100%;
  padding:25px 30px;
  &__menu{
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    &__item{
      margin-right: 26px;
      font-size: 16px;
      color:#111111;
    }
    &__item-active{
      font-size: 20px;
      font-weight: 600;
    }
    &__item-active::after{
      display: block;
      content: '';
      margin:0 auto;
      width: 90%;
      height: 3px;
      background-color: #ec4141;
    }
  }
  &__view{
    margin: 0 auto;
    min-width: 760px;
    max-width: 1100px;
  }
}
</style>
