<template lang="">
  <!-- 容器 -->
  <div class="swap">
  <!-- 输入手机和密码登录 -->
    <div class="swap__form">
      <!-- 手机号  -->
      <div class="swap__form__phone">
        <div class="swap__form__phone__icon iconfont">&#xe63b;</div>
        <input type="tel" class="swap__form__phone__input" required  placeholder="请输入手机号" v-model="phone">
      </div>
      <!-- 密码 -->
      <div class="swap__form__password">
        <div class="swap__form__password__icon iconfont">&#xe603;</div>
        <input type="password" class="swap__form__password__input" required placeholder="请输入密码" v-model="password">
      </div>
    </div>
    <!-- 选项 -->
    <div class="swap__options">
      <div class="swap__options__left">
        <input type="checkbox" class="swap__options__left__save" id="save" :v-model="isSave">
        <label for="save" class="swap__options__left__text">自动登录</label>
      </div>
      <div class="swap__options__right"><a href="https://music.163.com/" target="_blank">忘记密码</a></div>
    </div>
    <!-- 提示信息 -->
    <p class="swap__notice">
    {{notice}}
    </p>
    <!-- 登录按钮 -->
    <button class="swap__login" @click="checkForm">登录</button>
    <button class="swap__register">注册</button>
  </div>
</template>
<script>
import { getCellPhone } from '../../api/api_login'
export default {
  name: 'LoginPhone',
  data () {
    return {
      phone: '',
      password: '',
      isSave: false,
      notice: ''
    }
  },
  methods: {
    // 检查表单
    checkForm () {
      console.log(this.phone)
      if (/^[\d]{11}$/.test(this.phone) !== true) {
        this.notice = '手机号码错误'
        return false
      }
      console.log(this.password)
      if (/^\S{8,20}$/.test(this.password) !== true) {
        this.notice = '密码错误'
        return false
      }
      this.onSubmit()
    },
    // 提交手机号密码
    async onSubmit () {
      console.log('submit')
      const result = await getCellPhone(this.phone, this.password)
      if (result.code === 200) {
        console.log('before dispatch')
        await this.$store.commit('setAccount', result)
        console.log('after dispatch')
        this.$router.push('/')
        return null
      } else if (result.code === 502) {
        this.notice = '账号或密码错误'
        return null
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.swap{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  &__form{
    box-sizing: content-box;
    margin-bottom: 10px;
    height: 81px;
    width: 300px;
    border: 1px solid #222222;
    border-radius: 3px;
    &__phone{
      box-sizing: content-box;
      position: relative;
      height: 40px;
      border-bottom: 1px solid #222222;
      &__icon{
        position: absolute;
        left: 5px;
        width: 30px;
        text-align: center;
        line-height: 40px;
      }
      &__input{
        display: inline-block;
        border: 0;
        padding-left: 30px;
        line-height: 40px;
      }
    }
    &__password{
      position: relative;
      height: 40px;
      &__icon{
        position: absolute;
        left: 5px;
        width: 30px;
        text-align: center;
        line-height: 40px;
      }
      &__input{
        display: inline-block;
        border: 0;
        padding-left: 30px;
        line-height: 40px;
      }
    }
  }
  &__options{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 300px;
    height: 40px;
  }
  &__login{
    margin-bottom: 10px;
    width: 300px;
    height: 40px;
    border:0;
    border-radius: 5px;
    line-height: 40px;
    font-size: 24px;
    text-align: center;
    color:white;
    background-color: #ff3a3a;
    outline: none;
    cursor: pointer;
  }
  &__login:hover{
    background-color: #e53434;
  }
  &__register{
    border:0;
    font-size: 16px;
    text-align: center;
    background-color: white;
    cursor: pointer;
  }
  &__notice{
    height: 30px;
    color:#e53434;
  }
}
</style>
