import { createStore } from 'vuex'

// 将状态保存在本地
const setLocalState = (state) => {
  console.log('setlocalstate start')
  const result = JSON.stringify(state)
  localStorage.setItem('state', result)
}
const getLocalState = (state) => {
  console.log('getlocalstate start')
  let result
  if (!localStorage.state) {
    result = {
      isLogin: false,
      user: {},
      play: {}
    }
  } else {
    result = JSON.parse(localStorage.state)
  }
  return result
}

export default createStore({
  state: getLocalState(),
  mutations: {
    // 退出登录
    logout (state, payload) {
      state.isLogin = false
      state.user = {}
      if (document.cookie) {
        document.cookie = 'Max-Age: 0;'
      }
      setLocalState(state)
    },
    // 手机登录成功信息
    setAccount (state, payload) {
      // 解构赋值
      const { account, profile, cookie } = payload
      document.cookie = cookie
      state.user.account = account
      state.user.profile = profile
      state.isLogin = true
      setLocalState(state)
    },
    // 播放歌单
    playAllMusic (state, payload) {
      const { songList, toggle } = payload
      console.log('index', payload)
      // 判断是添加还是替换
      if (toggle) {
        // 替换
        state.play.musicList = songList
        state.play.currentIndex = 0
        console.log(songList, 'index')
        state.play.currentSongId = songList[0].id
        state.play.isPlay = true
        console.log('toggle true')
      } else {
        // 添加到当前播放歌曲之后
        state.play.musicList.splice(state.play.currentIndex, 0, ...songList)
      }
      setLocalState(state)
      console.log('playallmusic end')
    },
    // 播放单曲
    playMusic (state, music) {
      if (music.id === state.play.currentSongId) { return null }
      state.play.musicList = state.play.musicList.filter((item) => item.id !== music.id)
      state.play.musicList.splice(state.play.currentIndex + 1, 0, music)
      state.play.currentIndex += 1
      state.play.currentSongId = state.play.musicList[state.play.currentIndex].id
      setLocalState(state)
    },
    setIsPlay (state, value) {
      state.play.isPlay = value
      setLocalState(state)
    },
    // 歌曲前进后退
    changeMusic (state, value) {
      state.play.currentIndex += value
      if (state.play.currentIndex < 0) {
        state.play.currentIndex = state.play.musicList.length - 1
      } else if (state.play.currentIndex >= state.play.musicList.length) {
        state.play.currentIndex = 0
      }
      state.play.currentSongId = state.play.musicList[state.play.currentIndex].id
      setLocalState(state)
    },
    setVolume (state, volume) {
      state.play.saveVolume = volume
      setLocalState(state)
    }
  },
  actions: {
  },
  modules: {
  }
})
